import { Component, OnInit } from '@angular/core';
import {Observable} from 'rxjs';
import {AuthService} from '../auth.service';
import {Router} from '@angular/router';
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
forms;
elem;

  constructor(private auth:AuthService,private router:Router) { }

  ngOnInit() {
this.forms= document.getElementById('contact_form');
this.elem=document.getElementById('success_message');
  }
  submit(){
    let form=this.forms;
          let name = form["name"].value;          
          let user_password=form["user_password"].value;
          let confirm_password=form["confirm_password"].value;
          let email=form["email"].value;
          let contact_no=form["contact_no"].value;
          let address=form["address"].value;
          
          // let image=form["image"].value;
    if(this.formvalidate()){

    this.auth.registerUser({
      'status':2,
      'item':{
        'email':email
      }
    }).subscribe(data => {
      if(data[0])
      alert('email already exits');
      else
      {this.elem.style.display="block";
        this.auth.registerUser({'status':1,
         'item':
         {'name':name,        
        'user_password':user_password,
        'email':email,
        'contact':contact_no,
        'address':address,
        }
          }).subscribe( data => {
       this.router.navigate(['main']);
        });
      }
    })
 }else{
   this.elem.parentNode.parentNode.reset();
 }

  }



  formvalidate(){
    //let errors=this.errors;
    let form=this.forms;
          let name = form["name"].value;          
          let user_password=form["user_password"].value;
          let confirm_password=form["confirm_password"].value;
          let email=form["email"].value;
          let contact_no=form["contact_no"].value;
          let address=form["address"].value;          
          // let image=form["image"].value;

        //console.log(form['image']);

        if (name == "") {
            alert("Name must be filled out");
            return false;
        }
       
        if(user_password ==""){
          alert("Password must be filled out");
          return false;
        }
        if(user_password !== confirm_password){
          alert("Password do not match");
          return false;
        }
        if(email==""){
          alert("Email must be filled out");
          return false;
        }
        let pattern= /^[0-9a-zA-Z.]+[@]{1}[a-zA-Z]+[.][a-zA-Z]+$/;
         if(!pattern.test(email))
         {
           alert("Invalid email format");
           return false;
         }
        if(contact_no == ""){
          alert("Contact no. must be filled out");
          return false;
        }
        let cont = /[0-9]{10}/
        if(+contact_no == NaN){
          alert("Contact no. must be a number");
          return false;
        }
        if(!cont.test(contact_no)){
          alert("Invalid contact no.");
          return false;
        }
        if(address==""){
          alert("Address must be filled out");
          return false;
        }        
        // if(image==""){
        //   alert("Please select an image");
        //   return false;
        // }
        return true;
      }




}
