import { Component, OnInit } from '@angular/core';
import {DataService} from '../data.service';
import {Router} from '@angular/router';
import {Observable} from 'rxjs';
import {forkJoin} from 'rxjs';
import {map} from 'rxjs/operators';
import {GatewayService} from '../gateway.service';
@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css']
})
export class CartComponent implements OnInit {
  Cart;
  userid;
  productid;
  amount=0;
  checksum;
  pid=[];
  constructor(private dataservice:DataService,private gatewayservice:GatewayService,private router:Router) {

}
  ngOnInit() {

  this.dataservice.getcurrentuser().subscribe( user => {this.userid = user[0].id

  this.dataservice.getCart(this.userid).subscribe( product => {this.productid = product

     for(product of this.productid)
     {
      this.pid.push(product.productid);
     }
  if(this.pid.length!=0){


  this.dataservice.getDataofId(this.pid).subscribe( result => { this.Cart = result 
    this.amount=0;
     for(let elem of this.Cart)
     {
        this.amount += +elem.price;
     }
  });}
  });
  });


  }

  emptyCart(){
    this.dataservice.deleteCart(this.userid).subscribe( res => {
      if(res){
      alert("cart empty");
      this.router.navigate(['main']);}
    });
    this.Cart=[];

  }


  checkout(){
    var mobs: Observable<any>[] = [];
    var tobs: Observable<any>[]=[];

    this.gatewayservice.getBalance().subscribe( res => {
      if(res < this.amount)
      {
        alert("Insufficient balance");
        return;
      }
    })
    alert("Items buying.....");
    var itemlist:number[]=[];
    for(let elem of this.Cart){
      let message= "Book "+elem.name+" of price : "+elem.price+" /- is requested by the user";
      let sender=this.userid;
      let userid=elem.userId;
      mobs.push(this.dataservice.addToMessage({
        'sender':+sender,
        'product':message,
        'userid':+userid
      }));
      tobs.push(this.gatewayservice.pay({
        'from':this.userid,
        'to':elem.userId,
        'amount':+elem.price
      }));

      itemlist.push(elem.id);
    }
    forkJoin(tobs)
    .subscribe(dataArray => {
        // All observables in `observables` array have resolved and `dataArray` is an array of result of each observable
        for( let data of dataArray)
        { if (data != true)
              alert("something wrong") }
        this.dataservice.deleteListing(itemlist).subscribe( result => {
          if(!!result)
          {
            this.dataservice.deleteCart(this.userid).subscribe( res => {
              if(res){
              alert("Success");
              this.router.navigate(['main']);}
            });
            this.Cart=[];
          }
        })
        
    });

    

  }

}
