import { Component, OnInit } from '@angular/core';
import {GatewayService} from '../gateway.service';
import {DataService} from '../data.service';

@Component({
  selector: 'app-payment',
  templateUrl: './payment.component.html',
  styleUrls: ['./payment.component.css']
})
export class PaymentComponent implements OnInit {
cuser;
  constructor(private gateway:GatewayService,private dataservice:DataService) { }

  ngOnInit() {
   this.dataservice.getcurrentuser().subscribe( user => this.cuser = user[0].id)
  }
submit(){
  let amount = document.getElementById("amount").nodeValue;
  this.gateway.pay({
    'from':0,
    'to':this.cuser,
    'amount':amount
  }).subscribe( result => {console.log(result)
  if(result)
  document.getElementById("succes_message").style.display="block";
  });

}
}
